import React, { useState, useEffect } from 'react';
import { Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import axiosRetry from 'axios-retry';
import axios from 'axios';
import MetroModal from './MetroModal';
import { optionHyperscaler } from '../../constants/filterOptions';
import { RadioDD, BasicDD } from '../../components/customDropdown';
import CustomTable, { formatLongText } from '../../components/customTable/CustomTable';
import SparklineCustom from '../../components/chart/SparklineCustom';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import StackedColumnBar from '../../components/chart/StackedColumnBar';
import MapChart from '../../components/chart/MapChart';
import LineChart from '../../components/chart/LineChart';
import Legend from '../../components/legend/Legend';
import { API_BASE_URL } from '../../constants/config';
import { convertToUSDGrowth, convertToPercent, convertToWholePercent } from '../../constants/formatter';


const MetroContent = (props) => {
	const { metroData, tab_id } = props;

	const [idState, setReferenceID] = useState(null);
	const [modalShow, setModalShow] = useState(false);
	const [modalData, setModalData] = useState(null);
	const [selectedMetro, setSelectedMetro] = useState(null);
	const [toggleSelectedRow, setToggleSelectedRow] = useState();

	const byRank = metroData ? metroData.metro_ranking.by_rank : [];
	const byTopRank = metroData ? metroData.metro_ranking.by_top_rank : [];
	const byRankId = metroData ? metroData.metro_ranking.by_rank_id : [];
	const deploymentMap = metroData ? metroData.deployment_map : [];
	const contribution_to_total_usd_by_metro_tier = metroData ? metroData.contribution_to_total_usd_by_metro_tier : [];
	const y_by_y_percent_growth_by_metro_tier = metroData ? metroData.y_by_y_percent_growth_by_metro_tier : [];
	const metro_number_by_mrc_size = metroData ? metroData.metro_number_by_mrc_size : [];

	const [isLoading, setIsLoading] = useState(true);
	const [microStackedChart, setData] = useState(null);
	const [hqStackedChart, setHQRegionStackedChart] = useState(null);

	// radio for vertical values
	const option_group_by = [{
		key: 'hq_region',
		text: 'HQ Region'
	},
	{
		key: 'vertical',
		text: 'Vertical'
	}]

	// basic for hyperscaler values
	const hyperscaler_initial_value = optionHyperscaler ? optionHyperscaler[0] : null;
	const [hyperscaler_val, set_hyperscaler_val] = useState(hyperscaler_initial_value);

	// basic for group-by values
	const group_initial_value = option_group_by ? option_group_by[0] : null;
	const [group_val, set_group_val] = useState(group_initial_value);

	// Axios call
	const fetchData = () => {
		let url = `${API_BASE_URL}/charts/micro_stacked_column_chart/5-segments.json?print=pretty`;
		// Uncomment only when API is now available and working
		// const token = localStorage.getItem('token');
		// let url = `${API_BASE}/orbis/api/totalrevenuemetro`;
		// let defaultQueryParameters = `?measure=${measure_val_onApply.key}&range=${range_val_onApply.key}`;
		// let regionParameter = region_val_onApply.length > 0 ? `&region=${region_val_onApply.join(",").toString()}` : '';

		setIsLoading(true);
		axiosRetry(axios, { retries: 3 });

		axios({
			method: 'GET',
			url: url
			// Uncomment only when API is now available and working
			// url: url + defaultQueryParameters + regionParameter,
			// headers: {
			//   'Content-Type': 'application/json',
			//   "Authorization": `bearer ${token}`
			// }
		}).then(result => {
			// Uncomment only if for API testing purposes
			// console.log(result.config.url);
			setData(result.data);
			setIsLoading(false);
		}).catch(error => {
			console.log(error);
			setData(null);
			setIsLoading(false);
		});
	};

	useEffect(() => {
		fetchData();
	}, []);

	const indicatorPercent = (cell, row) => {
		let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
		let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;
		return (
			<span className={`d-inline-block indicator ${bgColor}`}>
				{icon}
				{convertToPercent(cell)}
			</span>
		);
	};

	const formatUSDGrowth = (cell, row) => {
		let new_cell = convertToUSDGrowth(cell);
		return formatLongText(new_cell, row)
	};

	const formatToPercent = (cell, row) => {
		return <span>{convertToPercent(cell)}</span>
	};

	const barchart = (cell, row) => {
		return <StackedColumnBar data={cell} />
	};

	const metro = (cell, row) => {
		return (
			<>
				{typeof cell !== "string"
					? <>
						<img className="flag-icon" src={cell.image_url} alt={cell.name} />
						<span className="body-name">{cell.name}</span>
					</>
					: <span className="text-semi">{formatLongText(cell, row)}</span>
				}
			</>
		)
	};

	const rankFormat = (cell, row, rowIndex, formatExtraData) => {
		return <span>{row.rank}</span>
	};

	// const handleSelected = (row) => {
	// 	let modalDataByRank;
	// 	let metroName = row.metro.name;

	// 	setSelectedMetro(metroName);

	// 	for (const key in byRankId) {
	// 		const element = byRankId[key];
	// 		console.log(element._id, row._id)
	// 		if (element._id === row._id) {
	// 			modalDataByRank = element
	// 		}
	// 	}

	// 	setModalShow(true);
	// 	setReferenceID(row._id);
	// 	setModalData(modalDataByRank);
	// }

	const onCloseModal = async () => {
		await setModalShow(false)
		await setSelectedMetro(null);
		await setReferenceID(null);
		await setToggleSelectedRow()

		setTimeout(() => {
			setModalData(null);
		}, 1000);

	}

	// const rowEvents = {
	// 	onClick: (e, row, rowIndex) => {
	// 		setToggleSelectedRow(row._id)
	// 		if (toggleSelectedRow) {
	// 			handleSelected(row)
	// 		}
	// 	}
	// };

	const handleOnSelect = (row, isSelect, rowIndex) => {
		let modalDataByRank;
		let metroName = row.metro.name;

		setToggleSelectedRow(row._id)
		setSelectedMetro(metroName);

		for (const key in byRankId) {
			const element = byRankId[key];

			if (element._id === row._id) {
				modalDataByRank = element
			}
		}

		if (isSelect) {
			setModalShow(true);
			setReferenceID(row._id);
			setModalData(modalDataByRank);
		}

		// TODO - UNCOMMENT  AXIOS CALL WHEN API FOR MAP FILTERING IS READY
		// for (const key in mdalRankingById) {
		//   const element = mdalRankingById[key];
		//   if (element._id === row._id) {
		//     modalDataByRank = element
		//   }
		// }
		// if (isSelect) {
		//   setModalShow(true);
		//   setReferenceID(row._id);
		//   setModalData(modalDataByRank);
		// }
	};

	const selectRow = {
		mode: 'radio',
		clickToSelect: true,
		hideSelectColumn: true,
		onSelect: handleOnSelect,
		classes: 'selected',
		bgColor: '#ecf2ff'
	};

	const rowClasses = (row) => {
		let classes = row._id === toggleSelectedRow ? 'selected' : 'selectable';
		return classes
	};

	const defaultSorted = [{
		dataField: 'mix_percent',
		order: 'desc',
	}];

	const columns = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		},
		{
			dataField: 'under',
			text: 'Rank',
			formatter: rankFormat,
			classes: 'rank',
			headerClasses: 'rank',
		},
		{
			dataField: 'metro',
			text: 'METRO',
			formatter: metro,
			classes: 'metro with-ellipsis',
			headerClasses: 'metro',
		},
		{
			dataField: 'stacked_column_bar_data',
			text: '',
			formatter: barchart,
			classes: 'sparkline-cell',
			headerClasses: 'sparkline-cell height-auto',
		},
		{
			dataField: 'mrc_usd',
			text: 'MRC $',
			sort: true,
			formatter: formatUSDGrowth,
			classes: 'right',
			headerClasses: 'right'
		},
		{
			dataField: 'mix_percent',
			text: 'MIX %',
			sort: true,
			formatter: formatToPercent,
			classes: 'border-right center',
			headerClasses: 'border-right center'
		},
		{
			dataField: 'q_by_q_growth_percent',
			text: 'Q/Q % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'center',
			headerClasses: 'center',
		},
		{
			dataField: 'q_to_q_usd_growth',
			text: 'Q-Q $ GROWTH',
			sort: true,
			formatter: formatUSDGrowth,
			classes: 'border-right right',
			headerClasses: 'border-right right'
		},
		{
			dataField: 'y_by_y_growth_percent',
			text: 'Y/Y % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'center',
			headerClasses: 'center',
		},
		{
			dataField: 'y_to_y_usd_growth',
			text: 'Y-Y $ GROWTH',
			sort: true,
			formatter: formatUSDGrowth,
			classes: 'right',
			headerClasses: 'right',
		}
	];

	const expandRow = {
		renderer: row => (
			<CustomTable
				data={row.under ? row.under : []}
				selectRow={selectRow}
				addHover
				// rowEvents={rowEvents}
				defaultSorted={defaultSorted}
				rowClasses={rowClasses}
				columns={columns}
			/>
		),
		expanded: [0, 1],
		showExpandColumn: true,
		expandByColumnOnly: true,
		onlyOneExpanding: true,
		expandHeaderColumnRenderer: ({ isAnyExpands }) => {
			return <div className="unclickable" onClick={(e) => { e.stopPropagation() }}></div>;
		},
		expandColumnRenderer: ({ expanded }) => {
			if (expanded) return <span>-</span>
			return <span>+</span>;
		}
	};

	const onLoadTopRanks = () => {
		return byTopRank;
		// TODO - UNCOMMENT  AXIOS CALL WHEN API FOR MAP FILTERING IS READY
		// return isRankingTableFiltered === true ? topTableData : byTopRank;
	};

	return (
		<>
			<MetroModal
				show={modalShow}
				onClose={onCloseModal}
				data={modalData}
				selectedMetro={selectedMetro}
				activeTab={tab_id}
			/>
			<Row className="pb-30">
				<Col md={4}>
					<Row className="pb-30">
						<Col md={12}>
							<Card className="revenue">
								<Card.Body>
									<Card.Title>deployment map</Card.Title>
									<Legend legend={deploymentMap.legend} />
									<div className="pt-30" style={{ height: "400px " }}>
										<MapChart
											id={`${tab_id}-map_chart`}
											largerWidth={false}
											data={deploymentMap.data}
										/>
									</div>
								</Card.Body>
							</Card>
						</Col>
					</Row>
					<Row>
						<Col md={12}>
							<Card className="revenue">
								<Card.Body>
									<Card.Title>contribution to total $ by metro tier</Card.Title>
									<Legend legend={contribution_to_total_usd_by_metro_tier.legend} />
									<div style={{ height: '250px' }}>
										<StackedColumnChart
											id={`${tab_id}-column_chart`}
											largerWidth={true}
											hideLabelBullet={true}
											isOneHundredPercent={true}
											isPercentage={true}
											insideLabel={false}
											percentLabel={false}
											hiddenValueAxis={false}
											data={contribution_to_total_usd_by_metro_tier.data}
											legend={contribution_to_total_usd_by_metro_tier.legend}
											isAnimationEnabled={false}
										/>
									</div>
								</Card.Body>
							</Card>
						</Col>
					</Row>
				</Col>
				<Col md={8}>
					<Card className="revenue-card">
						<Card.Body>
							<CustomTable
								tableTitle='metro ranking'
								tableName='metro-ranking scrollable'
								data={onLoadTopRanks() ? onLoadTopRanks().data : []}
								defaultSorted={defaultSorted}
								columns={columns}
								addSearch
								addLegends={<Legend legend={byTopRank.legend} />}
								addFilters={<>
									<BasicDD
										id="grop_by-table"
										options={option_group_by}
										value={group_val}
										setValue={set_group_val}
									/>
									<BasicDD
										id="hyperscaler-table"
										options={optionHyperscaler}
										value={hyperscaler_val}
										setValue={set_hyperscaler_val}
									/>
								</>}
								expandRow={expandRow}
							/>
						</Card.Body>
					</Card>
				</Col>
			</Row>
			<Row className="contentDivider">
				<Col md={6} xs={12}>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>Y/Y % growth by metro tier</Card.Title>
							<Legend legend={y_by_y_percent_growth_by_metro_tier.legend} />
							<div style={{ height: "300px" }}>
								<LineChart
									id={`${tab_id}-tier`}
									data={y_by_y_percent_growth_by_metro_tier.data}
									legend={y_by_y_percent_growth_by_metro_tier.legend}
									isAnimationEnabled={false}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
				<Col md={6} xs={12}>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>Metro # By MRC Size</Card.Title>
							<Legend legend={metro_number_by_mrc_size.legend} />
							<div style={{ height: "300px" }}>
								<StackedColumnChart
									id={`${tab_id}-mrc_size`}
									largerWidth={true}
									hiddenValueAxis={false}
									isOneHundredPercent={false}
									isPercentage={false}
									insideLabel={false}
									hideLabelBullet={true}
									data={metro_number_by_mrc_size.data}
									legend={metro_number_by_mrc_size.legend}
									isAnimationEnabled={false}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
			</Row>
		</>
	);
};

export default MetroContent;