import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import { Button, ButtonGroup, Container } from 'react-bootstrap';
import { FilterContainer, FilterModal } from '../../components/filter';
import { optionRange, optionRegion, optionHyperscaler } from '../../constants/filterOptions';
import { RadioDD, Separator, CheckboxDD } from '../../components/customDropdown';
import { comments } from '../../constants/comments';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { API_BASE_URL } from '../../constants/config';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import MetroContent from './MetroContent';


const Metro = ({ activeTab, setActiveTab }) => {
	const [isLoading, setIsLoading] = useState(true);
	const [grossData, setGrossData] = useState([]);
	const [pagData, setPAGData] = useState([]);
	const [netData, setNetData] = useState([]);


	// show all filters
	const [showFilter, setShowFilter] = useState(false);
	const onShowFilter = () => {
		setShowFilter(true);
	};
	const onCloseFilter = async () => {
		await onCancelRegion();
		await onCancelRange();
		await onCancelHyperscaler();
		setShowFilter(false);
	};
	const onApplyFilter = async () => {
		await onApplyRegion();
		await onApplyRange();
		await onApplyHyperscaler();
		setShowFilter(false);
	};

	// comment area
	const [showComment, setShowComment] = useState(false);
	const onCloseComment = () => {
		setShowComment(false);
	};
	const onShowComment = () => {
		setShowComment(true);
	};

	// checkbox for region values
	const [region_val_onApply, set_region_val_onApply] = useState([]);
	const [region_val, set_region_val] = useState([]);
	const onApplyRegion = () => {
		set_region_val_onApply(region_val);
	};
	const onCancelRegion = () => {
		set_region_val(region_val_onApply);
	};

	// radio for range values
	const range_initial_value = optionRange ? optionRange[0] : null;
	const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
	const [range_val, set_range_val] = useState(range_initial_value);
	const onApplyRange = () => {
		set_range_val_onApply(range_val);
	};
	const onCancelRange = () => {
		set_range_val(range_val_onApply);
	};

	// radio for hyperscaler values
	const hyperscaler_initial_value = optionHyperscaler ? optionHyperscaler[0] : null;
	const [hyperscaler_val_onApply, set_hyperscaler_val_onApply] = useState(hyperscaler_initial_value);
	const [hyperscaler_val, set_hyperscaler_val] = useState(hyperscaler_initial_value);
	const onApplyHyperscaler = () => {
		set_hyperscaler_val_onApply(hyperscaler_val);
	};
	const onCancelHyperscaler = () => {
		set_hyperscaler_val(hyperscaler_val_onApply);
	};

	// Axios call
	const fetchData = () => {
		let url = `${API_BASE_URL}/bookings/metro.json?print=pretty`;
		// Uncomment only when API is now available and working
		// const token = localStorage.getItem('token');
		// let url = `${API_BASE}/orbis/api/totalrevenuemetro`;
		// let defaultQueryParameters = `?measure=${measure_val_onApply.key}&range=${range_val_onApply.key}`;
		// let regionParameter = region_val_onApply.length > 0 ? `&region=${region_val_onApply.join(",").toString()}` : '';

		setIsLoading(true);
		axiosRetry(axios, { retries: 3 });

		axios({
			method: 'GET',
			url: url
			// Uncomment only when API is now available and working
			// url: url + defaultQueryParameters + regionParameter,
			// headers: {
			//   'Content-Type': 'application/json',
			//   "Authorization": `bearer ${token}`
			// }
		}).then(result => {
			// Uncomment only if for API testing purposes
			// console.log(result.config.url);
			let data = result.data;
			setGrossData(data['gross']);
			setPAGData(data['pag']);
			setNetData(data['net']);
			setIsLoading(false);
		}).catch(error => {
			console.log(error);
			setGrossData([]);
			setPAGData([]);
			setNetData([]);
			setIsLoading(false);
		});
	};

	useEffect(() => {
		fetchData();
	}, []);

	if (isLoading) return <Loader withContainer />;

	return (
		<>
			<div className={`page-content ${showComment ? 'open' : 'closed'}`}>
				{(grossData || pagData || netData) ?
					(grossData || pagData || netData).metro_ranking.by_top_rank.data.length > 0 ?
						<>
							<FilterContainer
								showComment={showComment}
								onShowFilter={onShowFilter}
								onShowComment={onShowComment}
							>
								<div className="filters">
									<ButtonGroup className="sub-tab">
										<Button
											variant='light'
											className={activeTab === 'gross' ? 'active' : ''}
											onClick={() => { setActiveTab('gross') }}>
											Gross Bookings
                		</Button>
										<Button
											variant='light'
											className={activeTab === 'pag' ? 'active' : ''}
											onClick={() => { setActiveTab('pag') }}>
											PAG
                		</Button>
										<Button
											variant='light'
											className={activeTab === 'net' ? 'active' : ''}
											onClick={() => { setActiveTab('net') }}>
											Net Bookings
                		</Button>
									</ButtonGroup>
									<Separator height='39px' />
									<CheckboxDD
										title="Deploy Region"
										id="region"
										options={optionRegion}
										valueApplied={region_val_onApply}
										checkVal={region_val}
										setCheckVal={set_region_val}
										onApply={onApplyRegion}
										onCancel={onCancelRegion}
									/>
									<RadioDD
										title="Time Period"
										id="range"
										options={optionRange}
										radioVal={range_val}
										setRadioVal={set_range_val}
										onApply={onApplyRange}
										onCancel={onCancelRange}
										initialValue={range_initial_value}
									/>
									<RadioDD
										id="hyperscaler"
										options={optionHyperscaler}
										radioVal={hyperscaler_val}
										setRadioVal={set_hyperscaler_val}
										onApply={onApplyHyperscaler}
										onCancel={onCancelHyperscaler}
										initialValue={hyperscaler_initial_value}
									/>
								</div>
								<FilterModal
									title="Deploy Metro"
									setShowFilter={setShowFilter}
									showFilter={showFilter}
									onCloseFilter={onCloseFilter}
									onApplyFilter={onApplyFilter}
								>
									<CheckboxDD
										title="Deploy Region"
										id="region-modal"
										options={optionRegion}
										visibleInModal
										checkVal={region_val}
										setCheckVal={set_region_val}
									/>
									<RadioDD
										title="Time Period"
										id="range-modal"
										options={optionRange}
										visibleInModal
										radioVal={range_val}
										setRadioVal={set_range_val}
									/>
									<RadioDD
										title="Hyperscaler"
										id="hyperscaler-modal"
										options={optionHyperscaler}
										visibleInModal
										radioVal={hyperscaler_val}
										setRadioVal={set_hyperscaler_val}
									/>
								</FilterModal>
							</FilterContainer>
							{/* ============= Your Component here! ============= */}
							<Container fluid className={`${activeTab === 'gross' ? 'd-block' : 'd-none'}`}>
								<MetroContent metroData={grossData} tab_id="gross-metro" />
							</Container>
							<Container fluid className={`${activeTab === 'pag' ? 'd-block' : 'd-none'}`}>
								<MetroContent metroData={pagData} tab_id="pag-metro" />
							</Container>
							<Container fluid className={`${activeTab === 'net' ? 'd-block' : 'd-none'}`}>
								<MetroContent metroData={netData} tab_id="net-metro" />
							</Container>
						</>
						: <ResourceNotFoundError redirectURL={"/executive-cockpit/bookings/metro"} />
					: <InternalServerError redirectURL={"/executive-cockpit/bookings/metro"} />}
			</div>
			<Comment
				data={comments}
				onCloseComment={onCloseComment}
				showComment={showComment}
			/>
		</>
	);
};

export default Metro;