import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import { Button, ButtonGroup, Container } from 'react-bootstrap';
import { optionRange, optionVertical2, optionRegion, optionMetro, optionHyperscaler } from '../../constants/filterOptions';
import { Separator, RadioDD, CheckboxDD, SearchCheckboxDD, ShowTopBottomDD } from '../../components/customDropdown';
import { FilterContainer, FilterModal } from '../../components/filter';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { API_BASE_URL } from '../../constants/config';
import { comments } from '../../constants/comments';
import Loader from '../../components/loader/Loader';
import Comment from '../../components/comment/Comment';
import CustomerContent from './CustomerContent';


const Customer = ({ activeTab, setActiveTab }) => {
	const [isLoading, setIsLoading] = useState(true)
	const [grossData, setGrossData] = useState([]);
	const [pagData, setPAGData] = useState([]);
	const [netData, setNetData] = useState([]);

	// show all filters
	const [showFilter, setShowFilter] = useState(false);
	const onShowFilter = () => {
		setShowFilter(true)
	}
	const onCloseFilter = async () => {
		await onCancelRegion();
		await onCancelMetro();
		await onCancelRange();
		await onCancelHyperscaler();
		await onCancelShowTopBottom();
		setShowFilter(false);
	}
	const onApplyFilter = async () => {
		await onApplyRegion();
		await onApplyMetro();
		await onApplyRange();
		await onApplyHyperscaler();
		await onApplyShowTopBottom();
		setShowFilter(false);
	}

	// comment area
	const [showComment, setShowComment] = useState(false);
	const onCloseComment = () => {
		setShowComment(false);
	};
	const onShowComment = () => {
		setShowComment(true);
	};

	// checkbox for region values
	const [region_val_onApply, set_region_val_onApply] = useState([]);
	const [region_val, set_region_val] = useState([]);
	const onApplyRegion = () => {
		set_region_val_onApply(region_val);
	};
	const onCancelRegion = () => {
		set_region_val(region_val_onApply);
	};

	// searchCheckbox for metro values
	const [metro_val_onApply, set_metro_val_onApply] = useState([]);
	const [metro_val, set_metro_val] = useState([]);
	const onApplyMetro = () => {
		set_metro_val_onApply(metro_val);
	};
	const onCancelMetro = () => {
		set_metro_val(metro_val_onApply);
	};

	// checkbox for vertical values
	const [vertical_val_onApply, set_vertical_val_onApply] = useState([]);
	const [vertical_val, set_vertical_val] = useState([]);
	const onApplyVertical = () => {
		set_vertical_val_onApply(vertical_val);
	};
	const onCancelVertical = () => {
		set_vertical_val(vertical_val_onApply);
	};

	// radio for range values
	const range_initial_value = optionRange ? optionRange[0] : null;
	const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
	const [range_val, set_range_val] = useState(range_initial_value);
	const onApplyRange = () => {
		set_range_val_onApply(range_val);
	};
	const onCancelRange = () => {
		set_range_val(range_val_onApply);
	};

	// radio for hyperscaler values
	const hyperscaler_initial_value = optionHyperscaler ? optionHyperscaler[0] : null;
	const [hyperscaler_val_onApply, set_hyperscaler_val_onApply] = useState(hyperscaler_initial_value);
	const [hyperscaler_val, set_hyperscaler_val] = useState(hyperscaler_initial_value);
	const onApplyHyperscaler = () => {
		set_hyperscaler_val_onApply(hyperscaler_val);
	};
	const onCancelHyperscaler = () => {
		set_hyperscaler_val(hyperscaler_val_onApply);
	};

	// for show top or bottom ranking
	const [showTopBottomVal_onApply, setTopBottomVal_onApply] = useState({
		topBottom: 'radio_top',
		numberInput: 10,
		rankingPercentile: 'radio_ranking'
	});
	const [showTopBottomVal, setTopBottomVal] = useState({
		topBottom: 'radio_top',
		numberInput: 10,
		rankingPercentile: 'radio_ranking'
	});
	const onApplyShowTopBottom = () => {
		setTopBottomVal_onApply(showTopBottomVal);
	};
	const onCancelShowTopBottom = () => {
		setTopBottomVal(showTopBottomVal_onApply);
	};

	// Axios call
	const fetchData = () => {
		let url = `${API_BASE_URL}/bookings/customer.json?print=pretty`;
		// const token = localStorage.getItem('token');
		// let url = `${API_BASE}/orbis/api/channelperformancepartner`;
		// let showParameter = `&show=${showTopBottomVal_onApply.topBottom.split('_')[1]}`;
		// let showValueParameter = `&showvalue=${showTopBottomVal_onApply.numberInput}`;
		// let defaultQueryParameters = `?measure=${measure_val_onApply.key}&range=${range_val_onApply.key}&vertical=${vertical_val_onApply.key}&hyperscaler=${hyperscaler_val_onApply.key}`;
		// let regionParameter = region_val_onApply.length > 0 ? `&region=${region_val_onApply.join(",").toString()}` : '';
		// let metroParameter = metro_val_onApply.length > 0 ? `&rmetro=${metro_val_onApply.join(",").toString()}` : '';

		setIsLoading(true);
		axiosRetry(axios, { retries: 3 });

		axios({
			method: 'GET',
			url: url
			// url: url + defaultQueryParameters + showParameter + showValueParameter + regionParameter + metroParameter,
			// headers: {
			//   'Content-Type': 'application/json',
			//   "Authorization": `bearer ${token}`
			// }
		}).then(result => {
			// Uncomment only if for API testing purposes
			// console.log(result.config.url);
			let data = result.data;
			setGrossData(data['gross']);
			setPAGData(data['pag']);
			setNetData(data['net']);
			setIsLoading(false);
		}).catch(error => {
			console.log(error);
			setGrossData([]);
			setPAGData([]);
			setNetData([]);
			setIsLoading(false);
		});
	};

	useEffect(() => {
		fetchData();
	}, []);

	if (isLoading) return <Loader withContainer />;

	return (
		<>
			<div className={`page-content customer-page ${showComment ? 'open' : 'closed'}`}>
				{(grossData || pagData || netData) ?
					(grossData || pagData || netData).customer_ranking.by_rank.data.length > 0 ?
						<>
							<FilterContainer
								showComment={showComment}
								onShowFilter={onShowFilter}
								onShowComment={onShowComment}
							>
								<div className="filters">
									<ButtonGroup className="sub-tab">
										<Button
											variant='light'
											className={activeTab === 'gross' ? 'active' : ''}
											onClick={() => { setActiveTab('gross') }}>
											Gross Bookings
                		</Button>
										<Button
											variant='light'
											className={activeTab === 'pag' ? 'active' : ''}
											onClick={() => { setActiveTab('pag') }}>
											PAG
                		</Button>
										<Button
											variant='light'
											className={activeTab === 'net' ? 'active' : ''}
											onClick={() => { setActiveTab('net') }}>
											Net Bookings
                		</Button>
									</ButtonGroup>
									<Separator height='39px' />
									<CheckboxDD
										title="Deploy Region"
										id="region"
										options={optionRegion}
										valueApplied={region_val_onApply}
										checkVal={region_val}
										setCheckVal={set_region_val}
										onApply={onApplyRegion}
										onCancel={onCancelRegion}
									/>
									<SearchCheckboxDD
										title="Deploy Metro"
										id="metro"
										options={optionMetro}
										valueApplied={metro_val_onApply}
										checkVal={metro_val}
										setCheckVal={set_metro_val}
										onApply={onApplyMetro}
										onCancel={onCancelMetro}
									/>
									<CheckboxDD
										title="Vertical"
										id="vertical"
										valueApplied={vertical_val_onApply}
										options={optionVertical2}
										checkVal={vertical_val}
										setCheckVal={set_vertical_val}
										onApply={onApplyVertical}
										onCancel={onCancelVertical}
									/>
									<RadioDD
										title="Time Period"
										id="range"
										options={optionRange}
										radioVal={range_val}
										setRadioVal={set_range_val}
										onApply={onApplyRange}
										onCancel={onCancelRange}
									/>
									<RadioDD
										id="hyperscaler"
										options={optionHyperscaler}
										radioVal={hyperscaler_val}
										setRadioVal={set_hyperscaler_val}
										onApply={onApplyHyperscaler}
										onCancel={onCancelHyperscaler}
									/>
									<ShowTopBottomDD
										id="show_ranking"
										showTopBottomVal={showTopBottomVal}
										setTopBottomVal={setTopBottomVal}
										onApply={onApplyShowTopBottom}
										onCancel={onCancelShowTopBottom}
									/>
								</div>
								<FilterModal
									title="Customer"
									setShowFilter={setShowFilter}
									showFilter={showFilter}
									onCloseFilter={onCloseFilter}
									onApplyFilter={onApplyFilter}
								>
									<CheckboxDD
										title="Deploy Region"
										id="region-modal"
										options={optionRegion}
										visibleInModal
										checkVal={region_val}
										setCheckVal={set_region_val}
									/>
									<SearchCheckboxDD
										title="Deploy Metro"
										id="metro-modal"
										options={optionMetro}
										visibleInModal
										checkVal={metro_val}
										setCheckVal={set_metro_val}
									/>
									<RadioDD
										title="Vertical"
										id="vertical-modal"
										options={optionVertical2}
										visibleInModal
										radioVal={vertical_val}
										setRadioVal={set_vertical_val}
									/>
									<RadioDD
										title="Time Period"
										id="range-modal"
										options={optionRange}
										visibleInModal
										radioVal={range_val}
										setRadioVal={set_range_val}
									/>
									<RadioDD
										title="Hyperscaler"
										id="hyperscaler-modal"
										options={optionHyperscaler}
										visibleInModal
										radioVal={hyperscaler_val}
										setRadioVal={set_hyperscaler_val}
									/>
									<ShowTopBottomDD
										title="Show Ranking"
										id="show_ranking-modal"
										showTopBottomVal={showTopBottomVal}
										visibleInModal
										setTopBottomVal={setTopBottomVal}
									/>
								</FilterModal>
							</FilterContainer>
							{/* ============= Your Component here! ============= */}
							<Container fluid className={`${activeTab === 'gross' ? 'd-block' : 'd-none'}`}>
								<CustomerContent customerData={grossData} tab_id='gross-customer'/>
							</Container>
							<Container fluid className={`${activeTab === 'pag' ? 'd-block' : 'd-none'}`}>
								<CustomerContent customerData={pagData} tab_id='pag-customer'/>
							</Container>
							<Container fluid className={`${activeTab === 'net' ? 'd-block' : 'd-none'}`}>
								<CustomerContent customerData={netData} tab_id='net-customer'/>
							</Container>
						</>
						: <ResourceNotFoundError redirectURL={"/executive-cockpit/bookings/customer"} />
					: <InternalServerError redirectURL={"/executive-cockpit/bookings/customer"} />}
			</div>
			<Comment
				data={comments}
				onCloseComment={onCloseComment}
				showComment={showComment}
			/>
		</>
	);
};

export default Customer;
