import React, { useState, useRef } from 'react';
import { v4 as uuidv4 } from 'uuid';
import './draggableComponent.scss';
import { useWidowResize } from '../../constants/utilityHooks';
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import { FormControl } from 'react-bootstrap';
import { BiChevronDown, BiChevronRight } from 'react-icons/bi';

const reorder = (list, startIndex, endIndex) => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);

  return result;
};

const move = (source, destination, droppableSource, droppableDestination) => {
  const sourceClone = Array.from(source);
  const destClone = Array.from(destination);
  const [removed] = sourceClone.splice(droppableSource.index, 1);

  destClone.splice(droppableDestination.index, 0, removed);

  const result = {};
  result[droppableSource.droppableId] = sourceClone;
  result[droppableDestination.droppableId] = destClone;

  return result;
};

const copy = (source, destination, droppableSource, droppableDestination) => {
  const sourceClone = Array.from(source);
  const destClone = Array.from(destination);
  const item = sourceClone[droppableSource.index];

  destClone.splice(droppableDestination.index, 0, { ...item, key: uuidv4() });

  return destClone;
};

const DraggableComponent = ({ buttonList, dragState, setDragState, children, withPreview }) => {
  const [searchVal, setSearchVal] = useState('');
  const [openDropdown, setOpenDropdown] = useState();
  const [focusSearch, setFocusSearch] = useState(false);

  // maxHeight drag component
  const dragRef = useRef(null);
  const { height } = useWidowResize(dragRef);

  const onToggleDD = (e) => {
    if (e === openDropdown) {
      setOpenDropdown()
    } else {
      setOpenDropdown(e);
    }
  };

  const onDragEnd = (result) => {

    const { source, destination, draggableId } = result;

    // dropped outside the list
    if (!destination) {
      return;
    }

    switch (source.droppableId) {
      case 'buttonList':
        let parentId = draggableId.split("----")[0];
        let arrayUnder = buttonList.filter(data => data.buttonKey === parentId)
        let getButtonUnder = arrayUnder.length > 0 ? arrayUnder[0].buttonUnder : [];

        setDragState({
          ...dragState,
          [destination.droppableId]: {
            ...dragState[destination.droppableId],
            items: copy(
              getButtonUnder,
              dragState[destination.droppableId].items,
              source,
              destination
            )
          }
        });
        break;
      // case destination.droppableId:
      //   setDragState({
      //     ...dragState,
      //     [destination.droppableId]: {
      //       ...dragState[destination.droppableId],
      //       items: reorder(
      //         dragState[source.droppableId],
      //         source.index,
      //         destination.index
      //       )
      //     }
      //   });
      //   break;
      default: console.log('move')
        // setDragState({
        //   ...dragState,
        //   [destination.droppableId]: {
        //     ...dragState[destination.droppableId],
        //     items: move(
        //       dragState[source.droppableId].items,
        //       dragState[destination.droppableId].items,
        //       source,
        //       destination
        //     )
        //   }
        // });
        break;
    }
  };

  // remove Item in fields
  const onRemoveField = (listId, objectID) => {
    let specificList = dragState[listId];
    let data = specificList.items ? specificList.items : [];
    let updated_data_after_delete = data.filter(item => item.key !== objectID);
    setDragState({
      ...dragState,
      [listId]: {
        ...dragState[listId],
        items: updated_data_after_delete
      }
    })
  }

  const addList = e => {
    setDragState({ ...dragState, [uuidv4()]: [] });
  };

  return (
    <>
      <div className={`draggable-component ${withPreview ? 'with-preview' : ''}`} style={{ maxHeight: height ? height : 'auto' }} ref={dragRef ? dragRef : null}>
        <DragDropContext onDragEnd={onDragEnd}>
          <div className="toggler-wrapper">
            <div className="toggler-cell custom-scroll" style={{ maxHeight: height ? height - 15 : 'auto' }}>
              <h2 className="title">Available FIELDS</h2>
              <FormControl
                onKeyUp={() => setFocusSearch(true)}
                onBlur={() => setFocusSearch(false)}
                placeholder={`Search for fields`}
                onChange={(e) => setSearchVal(e.target.value)}
                value={searchVal}
              />
              {buttonList.map((item, index) => (
                <div className="toggler" key={item.buttonKey}>
                  <button className="btn-drag-drop" onClick={() => onToggleDD(item.buttonKey)}>
                    <span>{item.buttonLabel}</span> {openDropdown === item.buttonKey || focusSearch || searchVal !== '' ? <BiChevronDown /> : <BiChevronRight />}
                  </button>
                  {openDropdown === item.buttonKey || focusSearch || searchVal !== '' ? (
                    <Droppable droppableId="buttonList" isDropDisabled={true}>
                      {(provided, snapshot) => {
                        return (
                          <div className={`menu-drag-drop ${snapshot.isDraggingOver ? 'isDraggingOver' : ''}`}
                            ref={provided.innerRef}
                            isDraggingOver={snapshot.isDraggingOver}>
                            {item.buttonUnder.map((under, index) => (
                              <Draggable
                                key={under.key}
                                draggableId={`${item.buttonKey}----${under.key}`}
                                index={index}
                                value={under.label}
                              >
                                {(provided, snapshot) => (
                                  <React.Fragment>
                                    <div className={`item ${snapshot.isDragging ? 'isDragging' : ''}`}
                                      ref={provided.innerRef}
                                      {...provided.draggableProps}
                                      {...provided.dragHandleProps}
                                      isDragging={snapshot.isDragging}
                                      style={
                                        provided
                                          .draggableProps
                                          .style
                                      }>
                                      {under.label}
                                    </div>
                                    {snapshot.isDragging && (
                                      <div className="clone">{under.label}</div>
                                    )}
                                  </React.Fragment>
                                )}
                              </Draggable>
                            )).filter(
                              (child) => {
                                return (
                                  !searchVal || child.props.value.toLowerCase().includes(searchVal)
                                )
                              }
                            )}
                          </div>
                        )
                      }}
                    </Droppable>
                  ) : null}
                </div>
              ))}


            </div>
          </div>

          <div className="content-wrapper">
            {/* <button className="Button" onClick={addList}>
                <svg width="24" height="24" viewBox="0 0 24 24">
                  <path
                    fill="currentColor"
                    d="M19,13H13V19H11V13H5V11H11V5H13V11H19V13Z"
                  />
                </svg>
                <span className="ButtonText">Add List</span>
              </button> */}
            <div className="drag-destination-area">
              {Object.entries(dragState).map(([listId, list], i) => {
                return (
                  <div className="content-cell">
                    <div key={listId} className="content custom-scroll">
                      <h2 className="title">{list.name}</h2>
                      {list.text && <p className="text">{list.text}</p>}
                      <Droppable droppableId={listId} key={listId}>
                        {(provided, snapshot) => (
                          <div className={`drag-container ${snapshot.isDraggingOver ? 'isDraggingOver' : ''}`}
                            ref={provided.innerRef}
                            isDraggingOver={snapshot.isDraggingOver}>
                            {dragState[listId].items.length > 0
                              ? dragState[listId].items.map(
                                (item, index) => (
                                  <Draggable
                                    key={item.key}
                                    draggableId={item.key}
                                    index={index}>
                                    {(provided, snapshot) => (
                                      <div className={`item ${snapshot.isDragging ? 'isDragging' : ''}`}
                                        ref={provided.innerRef}
                                        {...provided.draggableProps}
                                        {...provided.dragHandleProps}
                                        isDragging={snapshot.isDragging}
                                        style={
                                          provided
                                            .draggableProps
                                            .style
                                        }>
                                        {item.label}
                                        <button type="button" className="close" onClick={(e) => onRemoveField(listId, item.key)}>
                                          <span aria-hidden="true"></span>
                                          <span className="sr-only">Close</span>
                                        </button>
                                      </div>
                                    )}
                                  </Draggable>
                                )
                              )
                              : !provided.placeholder && (
                                <div className="notice">Drop items here</div>
                              )}
                            {provided.placeholder}
                          </div>
                        )}
                      </Droppable>
                    </div>
                  </div>
                )
              })}
            </div>
            {withPreview &&
              <div className="preview-area">
                {children}
              </div>
            }
          </div>
        </DragDropContext>
      </div>
    </>
  );
}

export default DraggableComponent;
