import React from 'react';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import { Modal, Row, Col } from 'react-bootstrap';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import Legend from '../../components/legend/Legend';
import MicroBarChart from '../../components/chart/MicroBarChart';
import CustomTable from '../../components/customTable/CustomTable';
import MapModalChart from '../../components/chart/MapModalChart';
import DonutChart from '../../components/chart/DonutChart';
import { convertToPercent, convertToUSDGrowth } from '../../constants/formatter';


const CustomerModal = (props) => {
  const { data, selectedCustomer, show, onClose, activeTab } = props;
  let modalName = activeTab;

  let overall = data ? data.overall : [];
  let tableDetails = data ? (data.vertical_details || data.metro_details || data.product_details) : [];
  let revenueMix = data ? (data.vertical_revenue_mix || data.metro_revenue_mix || data.product_revenue_mix) : [];
  let deploymentMap = data ? data.deployment_map : [];
  let total_bookings = revenueMix ? (revenueMix.gross_bookings || revenueMix.pag_bookings || revenueMix.net_bookings) : 0;
  let total_bookings_title = revenueMix.gross_bookings ? "GROSS BOOKINGS" : revenueMix.pag_bookings ? "PAG BOOKINGS" : revenueMix.net_bookings ? "NET BOOKINGS" : 'BOOKINGS';

  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;

    return (
      <>
        <span className={`indicator ${bgColor}`}>
          {icon}
          {convertToPercent(cell)}
        </span>
      </>
    );
  };

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  };

  const formatWholePercent = (cell, row) => {
    return (
      <span>
        {convertToPercent(cell)}
      </span>
    );
  };

  const microBarChart = (cell, row) => {
    return (
      <MicroBarChart loadedData={cell} id={"micro-bar-chart-" + row._id} />
    );
  }

  const metroColumn = (cell, row) => {
    return (
      <>
        <img className="flag-icon" src={cell.image_url} alt={cell.name} />
        <span className="body-name">{cell.name}</span>
      </>

    )
  };

  // For Ranks ===
  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return (
      <span>{rowIndex + 1}</span>
    );
  };
  // For Ranks ===

  // For Initial Sort ===
  const defaultSorted = [
    {
      dataField: 'mrr_mix_percent',
      order: 'desc', // or asc
    }
  ];
  // For Initial Sort ===

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '', // For Ranks
      text: 'Rank',
      align: 'center',
      headerAlign: 'center',
      classes: 'rank',
      headerClasses: 'rank',
      formatter: rankFormat,
    },
    {
      dataField: 'metro',
      text: 'METRO',
      align: 'left',
      headerAlign: 'left',
      classes: 'metro',
      headerClasses: 'metro',
      formatter: metroColumn
    },
    {
      dataField: 'micro_column_bar_data',
      text: '',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      classes: 'sparkline-cell',
      headerClasses: 'sparkline-cell',
      formatter: microBarChart
    },
    {
      dataField: 'mrc_usd',
      text: 'MRC $',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'percent_customer_total',
      text: '% CUSTOMER TOTAL',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatWholePercent
    },
    {
      dataField: 'percent_metro_total',
      text: '% METRO TOTAL',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatWholePercent
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q GROWTH%',
      sort: true,
      formatter: indicatorPercent,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'p4q_growth_percent',
      text: 'P4Q GROWTH%',
      sort: true,
      formatter: indicatorPercent,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y GROWTH%',
      sort: true,
      formatter: indicatorPercent,
      align: 'right',
      headerAlign: 'right'
    }
  ];

  if (modalName.includes('gross')) {
    modalName = 'Gross Bookings';
  } else if (modalName.includes('pag')) {
    modalName = 'PAG';
  } else if (modalName.includes('net')) {
    modalName = 'Net Bookings';
  }

  return (
    <Modal id="customer-modal" className="customer-modal"
      show={show}
      onHide={onClose}
      // size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header>
        <Modal.Title id="contained-modal-title-vcenter">
          {selectedCustomer} {modalName}
        </Modal.Title>
        <button type="button" className="close" onClick={onClose}>
          <span aria-hidden="true"></span><span className="sr-only">
            Close
          </span>
        </button>
      </Modal.Header>
      <Modal.Body>
        {data ?
          <>
            <Row>
              <Col className="fix-width" style={{ paddingRight: "30px" }}>
                <KPI_NEW
                  totalValue={overall.total.value}
                  totalUnit={overall.total.unit}
                  details={overall.details}
                />
              </Col>
              <Col md={6} xs={12}>
                <Legend legend={deploymentMap.legend} />
                <div style={{ height: "350px ", paddingTop: "2rem" }}>
                  <MapModalChart
                    id={"channel_bookings_metro_modal_map_chart"}
                    largerWidth={false}
                    data={deploymentMap.data}
                    isAnimationEnabled={false}
                  />
                </div>
              </Col>
              <Col style={{ paddingLeft: "30px" }}>
                <div style={{ height: "350px" }}>
                  <DonutChart
                    id={"channel_bookings_metro_modal_donut_chart"}
                    totalTitle={total_bookings_title}
                    total={total_bookings}
                    data={revenueMix.data}
                    legend={revenueMix.legend}
                    isDollar={true}
                    isAnimationEnabled={false}
                  />
                </div>
              </Col>
            </Row>
            <Row>
              <Col md={12} style={{ paddingTop: '1.5rem' }}>
                <CustomTable
                  tableName='vertical-details scrollable'
                  data={tableDetails === undefined ? [] : tableDetails}
                  defaultSorted={defaultSorted} // For Initial Sort ===
                  columns={columns}
                />
              </Col>
            </Row>
          </>
          : <div>No data available.</div>
        }
      </Modal.Body>
    </Modal>
  );
}

export default CustomerModal;