  //   let defaultParameters = `?measure=${measure_val_onApply.key}&range=${range_val_onApply.key}&vertical=${vertical_val_onApply}&hyperscaler=${hyperscaler_val_onApply}`;
  //   let showValueParameter = `&showvalue=${showTopBottomVal_onApply.numberInput}`;
  //   let showParameter = `&show=${showTopBottomVal_onApply.topBottom.split('_')[1]}`;
  //   let regionParameter = region_val_onApply.length > 0 ? `&region=${region_val_onApply.join(",").toString()}` : '';
  //   let metroParameter = metro_val_onApply.length > 0 ? `&rmetro=${metro_val_onApply.join(",").toString()}` : '';
  //   let mapRegionParameter = map_region_val != null ? `&mapregion=${map_region_val}` : '';

  //   setIsLoading(true);

  //   if (isRankingTableFiltered === true)  {
  //     axiosRetry(axios, { retries: 3 });

  //     axios({
  //       method: 'GET',
  //       url: url + defaultParameters + showValueParameter + showParameter + regionParameter + metroParameter + mapRegionParameter,
  //       headers: {
  //         'Content-Type': 'application/json',
  //         "Authorization": `bearer ${token}`
  //       }
  //     }).then(result => {
  //       let customer_ranking_table = result.data.customer_ranking;
  //       // Uncomment only if for API testing purposes
  //       // console.log(result.config.url);
  //       setIsLoading(false);
  //       setTableData(customer_ranking_table.by_rank.data);
  //       setModalRankingById(customer_ranking_table.by_rank_id);
  //     }).catch(error => {
  //       setIsLoading(false);
  //       console.log(error);
  //       setTableData([]);
  //     });

