/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import MetroContent from './MetroContent';
import {RadioDD, CheckboxDD} from '../../components/customDropdown/';
import {FilterContainer, FilterModal} from '../../components/filter';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { optionMeasure, optionRange, optionRegion, optionHyperscaler } from '../../constants/filterOptions';
import { comments } from '../../constants/comments';
import { API_BASE_URL } from '../../constants/config';


const Metro = () => {
	const [isLoading, setIsLoading] = useState(true)
	const [idState, setReferenceID] = useState(null);
	const [metroData, setData] = useState(null);
	const [modalShow, setModalShow] = useState(false);
	const [modalData, setModalData] = useState(null);

	// show all filters
	const [showFilter, setShowFilter] = useState(false);
	const onShowFilter = () => {
		setShowFilter(true);
	};
	const onCloseFilter = async () => {
		await onCancelRegion();
		await onCancelMeasure();
		await onCancelRange();
		await onCancelHyperscaler();
		setShowFilter(false);
	};
	const onApplyFilter = async () => {
		await onApplyRegion();
		await onApplyMeasure();
		await onApplyRange();
		await onApplyHyperscaler();
		setShowFilter(false);
	};

	// comment area
	const [showComment, setShowComment] = useState(false);
	const onCloseComment = () => {
		setShowComment(false);
	};
	const onShowComment = () => {
		setShowComment(true);
	};

	// checkbox for region values
	const [region_val_onApply, set_region_val_onApply] = useState([]);
	const [region_val, set_region_val] = useState([]);
	const onApplyRegion = () => {
		set_region_val_onApply(region_val);
	};
	const onCancelRegion = () => {
		set_region_val(region_val_onApply);
	};

	// radio for measure values
	const measure_initial_value = optionMeasure ? optionMeasure[0] : null;
	const [measure_val_onApply, set_measure_val_onApply] = useState(measure_initial_value);
	const [measure_val, set_measure_val] = useState(measure_initial_value);
	const onApplyMeasure = () => {
		set_measure_val_onApply(measure_val);
	};
	const onCancelMeasure = () => {
		set_measure_val(measure_val_onApply);
	};

	// radio for range values
	const range_initial_value = optionRange ? optionRange[0] : null;
	const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
	const [range_val, set_range_val] = useState(range_initial_value);
	const onApplyRange = () => {
		set_range_val_onApply(range_val);
	};
	const onCancelRange = () => {
		set_range_val(range_val_onApply);
	};

	// radio for group1 values
	const hyperscaler_initial_value = optionHyperscaler ? optionHyperscaler[0] : null;
	const [hyperscaler_val_onApply, set_hyperscaler_val_onApply] = useState(hyperscaler_initial_value);
	const [hyperscaler_val, set_hyperscaler_val] = useState(hyperscaler_initial_value);
	const onApplyHyperscaler = () => {
		set_hyperscaler_val_onApply(hyperscaler_val);
	};
	const onCancelHyperscaler = () => {
		set_hyperscaler_val(hyperscaler_val_onApply);
	};

	// Axios call
	const fetchData = () => {
		let url = `${API_BASE_URL}/channel-bookings-revenue/metro.json`;
		// const token = localStorage.getItem('token');
		// let url = `${API_BASE}/orbis/api/channelperformancemetro`;
		// let defaultQueryParameters = `?measure=${measure_val_onApply.key}&range=${range_val_onApply.key}&hyperscaler=${hyperscaler_val_onApply.key}`;
		// let regionParameter = region_val_onApply.length > 0 ? `&region=${region_val_onApply.join(",").toString()}` : '';

		setIsLoading(true);

		axiosRetry(axios, { retries: 3 });

		axios({
			method: 'GET',
			url: url
			// url: url + defaultQueryParameters + regionParameter,
			// headers: {
			//   'Content-Type': 'application/json',
			//   "Authorization": `bearer ${token}`
			// }
		}).then(result => {
			// Uncomment only if for API testing purposes
			// console.log(result.config.url);
			setData(result.data);
			setIsLoading(false);
		}).catch(error => {
			console.log(error);
			setData(null);
			setIsLoading(false);
		});
	};

	useEffect(() => {
		fetchData();
	},
		[]
		// [
		//   region_val_onApply,
		//   measure_val_onApply,
		//   range_val_onApply,
		//   hyperscaler_val_onApply    
		// ]
	);

	if (isLoading) return <Loader withContainer />;

	return (
		<>
			<div className={`page-content ${showComment ? 'open' : 'closed'}`}>
				{metroData ?
					metroData.metro_ranking.by_top_rank.data.length > 0 ?
						<>
							<FilterContainer
								showComment={showComment}
								onShowFilter={onShowFilter}
								onShowComment={onShowComment}
							>
								<div className="filters">
									<CheckboxDD
										title="Deploy Region"
										id="region"
										options={optionRegion}
										valueApplied={region_val_onApply}
										checkVal={region_val}
										setCheckVal={set_region_val}
										onApply={onApplyRegion}
										onCancel={onCancelRegion}
									/>
									<RadioDD
										title="Measure"
										id="measure"
										options={optionMeasure}
										radioVal={measure_val}
										setRadioVal={set_measure_val}
										onApply={onApplyMeasure}
										onCancel={onCancelMeasure}
									/>

									<RadioDD
										title="Time Period"
										id="range2"
										options={optionRange}
										radioVal={range_val}
										setRadioVal={set_range_val}
										onApply={onApplyRange}
										onCancel={onCancelRange}
										initialValue={range_initial_value}
									/>

									<RadioDD
										id="hyperscaler"
										options={optionHyperscaler}
										radioVal={hyperscaler_val}
										setRadioVal={set_hyperscaler_val}
										onApply={onApplyHyperscaler}
										onCancel={onCancelHyperscaler}
										initialValue={hyperscaler_initial_value}
									/>
								</div>

								<FilterModal
									title="Topline"
									setShowFilter={setShowFilter}
									showFilter={showFilter}
									onCloseFilter={onCloseFilter}
									onApplyFilter={onApplyFilter}
								>
									<CheckboxDD
										title="Deploy Region"
										id="region"
										options={optionRegion}
										visibleInModal
										checkVal={region_val}
										setCheckVal={set_region_val}
									/>
									<RadioDD
										title="Measure"
										id="measure"
										options={optionMeasure}
										visibleInModal
										radioVal={measure_val}
										setRadioVal={set_measure_val}
									/>
									<RadioDD
										title="Time Period"
										id="range2"
										options={optionRange}
										visibleInModal
										radioVal={range_val}
										setRadioVal={set_range_val}
									/>
									<RadioDD
										title="Hyperscaler"
										id="hyperscaler2"
										options={optionHyperscaler}
										visibleInModal
										radioVal={hyperscaler_val}
										setRadioVal={set_hyperscaler_val}
									/>
								</FilterModal>
							</FilterContainer>
							<MetroContent
								metroData={metroData}
								modalShow={modalShow}
								modalData={modalData}
								setReferenceID={setReferenceID}
								setModalData={setModalData}
								setModalShow={setModalShow}
							// measure_val_onApply={measure_val_onApply}
							// range_val_onApply={range_val_onApply}
							// region_val_onApply={region_val_onApply}
							// hyperscaler_val_onApply={hyperscaler_val_onApply}
							/>
						</>
						: <ResourceNotFoundError redirectURL={"/executive-cockpit/channel-bookings-revenue/metro"} />
					: <InternalServerError redirectURL={"/executive-cockpit/channel-bookings-revenue/metro"} />}
			</div>
			<Comment
				data={comments}
				onCloseComment={onCloseComment}
				showComment={showComment}
			/>
		</>
	);
};

export default Metro;
