import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import { Button, ButtonGroup, Container } from 'react-bootstrap';
import { FilterContainer, FilterModal } from '../../components/filter';
import { optionRange, optionVertical, optionShowBy1 } from '../../constants/filterOptions';
import { RadioDD, Separator } from '../../components/customDropdown'
import { comments } from '../../constants/comments';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { API_BASE_URL } from '../../constants/config';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import GrowthContent from './GrowthContent';


const Growth = ({ activeTab, setActiveTab }) => {
	const [isLoading, setIsLoading] = useState(true);
	const [isChanged, setIsChanged] = useState(true);
	const [grossData, setGrossData] = useState([]);
	const [pagData, setPAGData] = useState([]);
	const [netData, setNetData] = useState([]);
	const [selectedData, setSelectedData] = useState(null);

	// show all filters
	const [showFilter, setShowFilter] = useState(false);
	const onShowFilter = () => {
		setShowFilter(true)
	}
	const onCloseFilter = async () => {
		await onCancelRange();
		await onCancelVertical();
		await onCancelShowBy();
		setShowFilter(false);
	}
	const onApplyFilter = async () => {
		await onApplyRange();
		await onApplyVertical();
		await onApplyShowBy();
		setShowFilter(false);
	}

	// comment area
	const [showComment, setShowComment] = useState(false);
	const onCloseComment = () => {
		setShowComment(false);
	};
	const onShowComment = () => {
		setShowComment(true);
	};

	// radio for range values
	const range_initial_value = optionRange ? optionRange[0] : null;
	const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
	const [range_val, set_range_val] = useState(range_initial_value);
	const onApplyRange = () => {
		set_range_val_onApply(range_val);
	};
	const onCancelRange = () => {
		set_range_val(range_val_onApply);
	};

	// radio for vertical values
	const vertical_initial_value = optionVertical ? optionVertical[0] : null;
	const [vertical_val_onApply, set_vertical_val_onApply] = useState(vertical_initial_value);
	const [vertical_val, set_vertical_val] = useState(vertical_initial_value);
	const onApplyVertical = () => {
		set_vertical_val_onApply(vertical_val)
	};
	const onCancelVertical = () => {
		set_vertical_val(vertical_val_onApply)
	};

	// radio for show by values
	const showBy_initial_value = optionShowBy1 ? optionShowBy1[0] : null;
	const [showBy_val_onApply, set_showBy_val_onApply] = useState(showBy_initial_value);
	const [showBy_val, set_showBy_val] = useState(showBy_initial_value);
	const onApplyShowBy = () => {
		set_showBy_val_onApply(showBy_val)
	};
	const onCancelShowBy = () => {
		set_showBy_val(showBy_val_onApply)
	};

	// Axios call
	const fetchData = () => {
		let url = `${API_BASE_URL}/bookings/growth.json?print=pretty`;
		// Uncomment only when API is now available and working
		// const token = localStorage.getItem('token');
		// let url = `${API_BASE}/orbis/api/totalrevenuegrowth`;
		// let defaultQueryParameters = `?measure=${measure_val_onApply.key}&range=${range_val_onApply.key}&showby=${group1_val_onApply.key}`;

		setIsLoading(true);
		axiosRetry(axios, { retries: 3 });

		axios({
			method: 'GET',
			url: url
			// Uncomment only when API is now available and working
			// url: url + defaultQueryParameters,
			// headers: {
			//   'Content-Type': 'application/json',
			//   "Authorization": `bearer ${token}`
			// }
		}).then(result => {
			const data = result.data;
			// Uncomment only if for API testing purposes
			// console.log(result.config.url);
			setGrossData(data['gross']);
			setPAGData(data['pag']);
			setNetData(data['net']);
			setIsLoading(false);
			setIsChanged(true);
			setSelectedData(null);
		}).catch(error => {
			console.log(error);
			setGrossData([]);
			setPAGData([]);
			setNetData([]);
			setIsLoading(false);
		});;
	};

	useEffect(() => {
		fetchData();
	}, []);

	if (isLoading) return <Loader withContainer />;

	return (
		<>
			<div className={`page-content growth-page ${showComment ? 'open' : 'closed'}`}>
				{(grossData || pagData || netData) ?
					(grossData || pagData || netData).segment_ranking.by_rank.length > 0 ?
						<>
							<FilterContainer
								showComment={showComment}
								onShowFilter={onShowFilter}
								onShowComment={onShowComment}
							>
								<div className="filters">
									<ButtonGroup className="sub-tab">
										<Button
											variant='light'
											className={activeTab === 'gross' ? 'active' : ''}
											onClick={() => { setActiveTab('gross') }}>
											Gross Bookings
										</Button>
										<Button
											variant='light'
											className={activeTab === 'pag' ? 'active' : ''}
											onClick={() => { setActiveTab('pag') }}>
											PAG
										</Button>
										<Button
											variant='light'
											className={activeTab === 'net' ? 'active' : ''}
											onClick={() => { setActiveTab('net') }}>
											Net Bookings
										</Button>
									</ButtonGroup>
									<Separator height='39px' />
									<RadioDD
										id="range"
										options={optionRange}
										radioVal={range_val}
										setRadioVal={set_range_val}
										onApply={onApplyRange}
										onCancel={onCancelRange}
										initialValue={range_initial_value}
									/>
									<RadioDD
										id="segment"
										options={optionVertical}
										radioVal={vertical_val}
										setRadioVal={set_vertical_val}
										onApply={onApplyVertical}
										onCancel={onCancelVertical}
										initialValue={vertical_initial_value}
									/>
									<RadioDD
										id="show"
										options={optionShowBy1}
										radioVal={showBy_val}
										setRadioVal={set_showBy_val}
										onApply={onApplyShowBy}
										onCancel={onCancelShowBy}
										initialValue={showBy_initial_value}
									/>
								</div>
								<FilterModal
									title="Growth"
									setShowFilter={setShowFilter}
									showFilter={showFilter}
									onCloseFilter={onCloseFilter}
									onApplyFilter={onApplyFilter}
								>
									<RadioDD
										title="Time Period"
										id="range-modal"
										options={optionRange}
										visibleInModal
										radioVal={range_val}
										setRadioVal={set_range_val}
									/>
									<RadioDD
										title="Segmentation"
										id="segment-modal"
										options={optionVertical}
										visibleInModal
										radioVal={vertical_val}
										setRadioVal={set_vertical_val}
									/>
									<RadioDD
										title="Show by"
										id="show-modal"
										options={optionShowBy1}
										visibleInModal
										radioVal={showBy_val}
										setRadioVal={set_showBy_val}
									/>
								</FilterModal>
							</FilterContainer>
							{/* ============= Your Component here! ============= */}
							<Container fluid className={`${activeTab === 'gross' ? 'd-block' : 'd-none'}`}>
								<GrowthContent
									selectedData={selectedData}
									resultData={grossData.segment_ranking}
									setSelectedData={setSelectedData}
									growthData={grossData}
									isChanged={isChanged}
									setIsChanged={setIsChanged}
									tab_id="gross-growth"
								/>
							</Container>
							<Container fluid className={`${activeTab === 'pag' ? 'd-block' : 'd-none'}`}>
								<GrowthContent
									selectedData={selectedData}
									resultData={pagData.segment_ranking}
									setSelectedData={setSelectedData}
									growthData={pagData}
									isChanged={isChanged}
									setIsChanged={setIsChanged}
									tab_id="pag-growth"
								/>
							</Container>
							<Container fluid className={`${activeTab === 'net' ? 'd-block' : 'd-none'}`}>
								<GrowthContent
									selectedData={selectedData}
									resultData={netData.segment_ranking}
									setSelectedData={setSelectedData}
									growthData={netData}
									isChanged={isChanged}
									setIsChanged={setIsChanged}
									tab_id="net-growth"
								/>
							</Container>
						</>
						: <ResourceNotFoundError redirectURL={"/executive-cockpit/bookings/growth"} />
					: <InternalServerError redirectURL={"/executive-cockpit/bookings/growth"} />}
			</div>
			<Comment
				data={comments}
				onCloseComment={onCloseComment}
				showComment={showComment}
			/>
		</>
	);
};

export default Growth;